
<template>
  <div class="swiper-container">
    <v-container fluid class="visual swiper-wrapper" v-bind:class="[$route.name]" ref="visual">
      <div name="block0" class="block block0 swiper-slide">
        <keep-alive>
          <transition name="slide-left" v-if="!$store.state.isMobile">
            <router-view name="router0"></router-view>
          </transition>
          <transition name="fade" v-else>
            <router-view name="router0"></router-view>
          </transition>
        </keep-alive>
      </div>
      <div name="block1" class="block block1 swiper-slide">
        <keep-alive>
          <transition name="slide-right" v-if="!$store.state.isMobile">
            <router-view name="router1"></router-view>
          </transition>
          <transition name="fade" v-else>
            <router-view name="router1"></router-view>
          </transition>
        </keep-alive>
      </div>
      <div name="block2" class="block block2 swiper-slide">
        <keep-alive>
          <transition name="slide-bottom" v-if="!$store.state.isMobile">
            <router-view name="router2"></router-view>
          </transition>
          <transition name="fade" v-else>
            <router-view name="router2"></router-view>
          </transition>
        </keep-alive>
      </div>
      <div name="block3" class="block block3 swiper-slide">
        <keep-alive>
          <transition name="slide-top" v-if="!$store.state.isMobile">
            <router-view name="router3"></router-view>
          </transition>
          <transition name="fade" v-else>
            <router-view name="router3"></router-view>
          </transition>
        </keep-alive>
      </div>

      <div class="swiper-pagination"></div>
    </v-container>
  </div>
</template>

<script>
import Swiper from "swiper/dist/js/swiper.esm.bundle";
import Velocity from "velocity-animate";
import store from "../store";
import router from "../router";
//import Guide from "@/components/Guide.vue";
// import LandingBar from "@/components/Landing.vue";
// const default_layout = 'default'
export default {
  // components: { Guide, LandingBar },
  data() {
    return {
      //isLoad: false,
      transitionName: "slide-top",
      items: []
    };
  },
  created() {
    // 뷰가 생성되고 데이터가 이미 감시 되고 있을 때 데이터를 가져온다.
    // this.fetchData()
  },
  destroyed() {},
  watch: {
    // 라우트가 변경되면 메소드를 다시 호출됩니다.
    // '$route': 'fetchData'
  },
  mounted() {
    let idx = null;
    if (this.$store.state.items <= 0) {
      store.commit("items", {
        array: this.fn_array(this.$refs["visual"])
      });
    }

    if (this.$store.state.isMobile) {
      // console.log("mounted isMobile//", this.$store.state.isMobile);
      const self = this;
      const swiper = new Swiper(".swiper-container", {
        // autoHeight: true,
        direction: "vertical",
        //mousewheel: true,
        keyboard: {
          enabled: true
        },
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      });
    }

    router.beforeEach((to, from, next) => {
      idx = to.meta.id;
      if (this.$store.state.isMobile || to.meta.layout == "detail") {
        next();
        return;
      } else {
        //this.isLoad = true;
      }

      if (to.path.split("/").length > 2) {
        this.transitionName = to.meta.transition;
        next();
      } else {
        /* const tr = this.transitionName.split("-");
        this.transitionName = tr[0];
        if (tr[1] === "top") {
          this.transitionName = this.transitionName + "-" + "bottom";
        } else if (tr[1] === "right") {
          this.transitionName = this.transitionName + "-" + "left";
        } else if (tr[1] === "bottom") {
          this.transitionName = this.transitionName + "-" + "top";
        } else if (tr[1] === "left") {
          this.transitionName = this.transitionName + "-" + "right";
        } else if (tr[1] === "in") {
          this.transitionName = this.transitionName + "-" + "out";
        } else if (tr[1] === "out") {
          this.transitionName = this.transitionName + "-" + "in";
        } else {
          this.transitionName = "slide-left";
        } */
        this.fn_sub(
          next,
          this.$store.state.itemsValue[idx],
          this.$store.state.items,
          idx
        );
      }
    });

    router.afterEach((to, from) => {
      window.scrollTo(0, 0);
      //this.isLoad = false;
    });

    // window.addEventListener("resize", this.fn_resize, { passive: true });
  },
  computed: {},
  beforeDestroy() {
    /* if (typeof window !== "undefined") {console.log('beforeDestroy')
      window.removeEventListener("resize", this.fn_resize, { passive: true });
    } */
  },
  methods: {
    fn_resize() {
      console.log("fn_resize def");
      // console.log("isMobile//", this.$store.state.isMobile);
    },
    fetchData() {
      // 데이터 가져오기 위한 유틸리티/API 래퍼로 변경합니다.
    },
    fn_array(elements) {
      let arr = [];
      for (var i = 0; i < elements.childNodes.length - 1; i++) {
        arr[i] = elements.childNodes[i];
      }
      return arr;
    },
    fn_sub: function(next, itemsValue, items, idx) {
      // console.log("Velocity fn_sub--------------------------",itemsValue);
      const vLangth = items.length - 1;
      if (idx > 0) {
        items.forEach(function(element, index) {
          if (element.children.length > 0) {
            element.children[0].classList.add("hide");
          }
          // element.style.willChange = 'width, height, transform, opacity';
          Velocity(
            element,
            {
              // width: itemsValue.width[index],
              // height: itemsValue.height[index],
              "max-width": itemsValue.width[index],
              "max-height": itemsValue.height[index],
              left: itemsValue.x[index],
              top: itemsValue.y[index]
            },
            {
              duration: 300,
              // easing: [.4, 0, .2, 1],
              easing: [0.25, 0.8, 0.5, 1],
              complete: function() {
                if (element.children.length > 0) {
                  element.children[0].classList.remove("hide");
                }
                // element.removeAttribute("style");
                if (index === vLangth || idx === 0) {
                  next();
                }
              }
            }
          );
        });
      } else {
        items[0].children[0].classList.add("hide");
        Velocity(
          items[0],
          {
            // width: itemsValue.width[0],
            // height: itemsValue.height[0],
            "max-width": itemsValue.width[0],
            "max-height": itemsValue.height[0]
          },
          {
            duration: 400,
            // easing: [0.25, 0.8, 0.5, 1],
            easing: [0.25, 0.8, 0.5, 1],
            complete: function() {
              items[0].children[0].classList.remove("hide");
              items[0].removeAttribute("style");
              next();
            }
          }
        );
      }
    }
  }
};
</script>
<style lang="scss" scoped>
@media (max-width: 750px) {
  .swiper-container {
    height: 100%;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
    list-style: none;
    padding: 0;
    z-index: 1;

    .swiper-wrapper {
      height: 100%;
      .swiper-pagination {
        position: absolute;
        text-align: center;
        transition: opacity 0.3s;
        transform: translateZ(0);
        z-index: 10;
      }
    }
    .Landing {
      .block0 {
        display: block;
      }
      .block1,
      .block2,
      .block3 {
        display: none;
      }
    }
  }
}
</style>
